<template>
  <div class="material">
    <div v-if="$route.name === '广告素材'">
      <Button type="success" size="large" to="/manage/links/material/create" class="create">添加素材</Button>
      <Table class="material-table" :columns="materialHeader" :data="materialData"></Table>
      <Page show-total :total="dataList.length" :page-size="10" @on-change="changePage" />
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        materialData: [],
        dataList: [],
        materialHeader: [
          {
            key: "id",
            title: "素材id",
            align: "center"
          },
          {
            key: "title",
            title: "素材标题",
            align: "center"
          },
          {
            key: "novelLink",
            title: "小说链接",
            align: "center",
            width: 500
          },
          {
            title: "Action",
            align: "center",
            width: 160,
            render: (h, params) => {
              let that = this;
              return h("div", [
                h( "Button", {
                  style: {
                    marginRight: "10px"
                  },
                  props: {
                    type: "primary",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.$router.push({
                        name: "修改广告素材",
                        params: {
                          row: params.row,
                          index: params.index
                        }
                      });
                    }
                  }
                }, "修改" ),
                h( "Button", {
                  style: {
                    marginRight: "10px"
                  },
                  props: {
                    type: "error",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.deleteOne(params.row, params.index);
                    }
                  }
                }, "删除" )
              ]);
            }
          }
        ]
      };
    },
    mounted() {
      this.showDataList();
    },
    methods: {
      showDataList() {
        this.$axios.get("/adMaterial").then(res => {
          if(res.data.code === 1) {
            this.dataList = res.data.data.reverse();
            this.materialData = this.dataList.slice(0, 10);
          }
        });
      },
      deleteOne(row, index) {
        let that = this;
        this.$Modal.confirm({
          title: '提示:',
          content: '确认删除这条内容吗？',
          onOk: () => {
            that.$axios.delete('/adMaterial', {params: {id: row._id}})
              .then(res => {
                that.$Message.info(res.data.success);
                that.materialData.splice(index, 1)
              });
          }
        })
      },
      changePage(index){
        var _start = ( index - 1 ) * 10;
        var _end = index * 10;
        this.materialData = this.dataList.slice(_start,_end);
        this.goTop()
      },
      goTop () {
        let self = this
        self.timer = setInterval(function () {
          let osTop = document.documentElement.scrollTop || document.body.scrollTop
          let ispeed = Math.floor(-osTop)
          document.documentElement.scrollTop = document.body.scrollTop = osTop + ispeed
          self.isTop = true
          if (osTop === 0) {
            clearInterval(self.timer)
          }
        }, 30)
      }
    },
    watch: {
      $route(to, from) {
        if(this.$route.params.state != 'cancel') {
          if(from.name == '创建广告素材') {
            this.materialData.unshift(this.$route.params)
          } else if (from.name == '修改广告素材') {
            this.materialData.splice(this.$route.params.index, 1, this.$route.params.row)
          }
        }
      }
    }
  };
</script>


<style scoped>
  .material-table {
    margin: 20px 0;
  }

  .tuiguang-tag {
    display: block;
    text-align: left;
    margin: 10px 0;
  }
</style>
